.usersContainer{
    max-width: 600px;
    min-width: 0;
    border-right: 1px solid var(--primary-border-color);
}
.usersTitle {
    font-size: 20px;
    font-weight: 700;
    line-height: 24px;
    color: var(--primary-text-color);
    -webkit-backdrop-filter:  blur(12px);
    backdrop-filter: blur(12px);
    background-color: var(--profile-top-blurred);
    height: 53px;
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 0 16px;
    position: sticky;
    top: 0;
    left: 0;
    z-index: 1;
}