.lockedScrollbar{
    overflow-y: hidden;
    height: 100%;
}
.profileInfoContainer {
    background-color: var(--primary-background-color);
    padding: 12px 16px 0 16px;
    margin-bottom: 16px;
}

.topInfoPanel {
    display: flex;
    justify-content: space-between;
    max-height: 69px;
    height: 15vw;
    width: 100%;
    min-height: 40px;
    min-width: 0;
}
.editProfileButtonWrapper {
    display: flex;
    justify-content: center;
    align-items: start;
}

.editProfileButton {
    color: var( --primary-text-color);
    background-color: var(--primary-background-color);
    border-radius: 20px;
    padding: 9px 18px 9px 18px;
    cursor: pointer;
    border: 1px solid rgb(83, 100, 113);
    transition: background-color 0.2s ease-in-out
}

.editProfileButton:hover {
    background-color: var(--hover-button-background-color);
}

.editProfileButton span {
    font-size: 15px;
    font-family: 'Helvetica Neue', sans-serif;
    font-weight: bold;
}

.name {
    color: var( --primary-text-color);
    font-size: 20px;
    font-weight: 800;
    font-family: TwitterChirp, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
    margin-bottom: 12px;
    display: flex;
    justify-content: start;
}

.name span {
    font: inherit;
}
.statusWrapper{
    font-family: TwitterChirp, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
    font-size: 15px;
    font-weight: 400;
    margin-bottom: 12px;
    display: flex;
    width: fit-content;
    align-items: start;
}
.status{
    color: var( --primary-text-color);
    display: inline-block;
    word-break: break-word;
    overflow: hidden;
}
.dataContainer {
    display: inline-block;
    font-size: 15px;
    font-family: TwitterChirp, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
    font-weight: 400;
}

.dataContainer span {
    color: var(--secondary-text-color);
}
.editProfileBgShadow{
    background-color: var(--background-prevent-blur);
    position: fixed;
    bottom: 0;
    top: 0;
    right: 0;
    left: 0;
    z-index: 0;
}
.editProfileContainer{
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    overscroll-behavior: none;
    border-radius: 16px;
    z-index: 2;
}
.scrollbarDecorator{
    position: absolute;
    z-index: 1;
    overflow: hidden;
    border-radius: 16px;
}
.editProfileWrapper{
    color-scheme: var(--color-scheme);
    scrollbar-color: var(--scrollbar-color);
    min-width: 600px;
    min-height: 400px;
    max-height: 90vh;

    width: 600px;
    height: 650px;
    z-index: 10;
    background-color: var(--primary-background-color);
    overflow-y: auto;
    overflow-x: hidden;
}

.scrollbarDecorator {
    color-scheme: var(--color-scheme);
}

