.contentWrapper {
    display: grid;
    grid-template-areas: "navbar feed";
    grid-template-columns: 2fr 10fr;
    grid-template-rows: 1fr;
    min-height: 100vh;
    width: 100%;
    max-width: 100%;
    min-width: 0;
    box-sizing: border-box;
    background-color: var(--primary-background-color);
    position: relative;
}