.container{
    padding: 0 16px;
    border-right: 1px solid var(--primary-border-color);
    max-width: 600px;
    width: 100%;
    box-sizing: border-box;
}
.dialogName{
    font-size: 17px;
    color: var(--primary-text-color);
    font-weight: 700;
    line-height: 20px;
    height: 53px;
    display: flex;
    justify-content: start;
    align-items: center;
    border-bottom: 1px solid var(--primary-border-color);
    margin-bottom: 16px;
}