.dialogItemContainer {
    color: var(--primary-text-color);
    grid-area: dialog;
    transition-duration: 0.2s;
    font-size: 15px;
    font-weight: 500;
    box-sizing: border-box;
}
.dialogItemContainer:last-child {
    margin-bottom: 0;
}
.dialogItemContainer .dialogItemWrapper.active{
    background: var(--send-message-background-color);
    border-right: 1px solid var(--message-backgorund-color);
}
.dialogItemContainer:hover .dialogItemWrapper{
    background-color: var(--dialog-item-background-hover);

}
.dialogItemWrapper{
    user-select: none;
    display: flex;
    overflow: hidden;
    box-sizing: border-box;
    transition-duration: 0.2s;
    text-decoration: none;
    padding: 10px;
    color: var(--primary-text-color);
}
.userLastMessage{
    color: var(--secondary-text-color);
}
.opponentLastMessage{
    color: var(--primary-text-color);
}
.underMessage{
    display: flex;
    overflow: hidden;
}
.underMessage span{
    white-space: nowrap;
    overflow: hidden;
    display: inline;
}
.draft{
    margin-right: 4px;
    color: #d55151;
}
.dialogPicture{
    height: 40px;
    width: 40px;
    border-radius: 9999px;
    margin-right: 12px;
}
.dialogInfo{
    overflow: hidden;
    max-width: 75%;
}
